import React, { useEffect, useRef, useState } from "react"
import Draggable from "react-draggable"

import {map_range} from "../TestData/functions"


function GradientView() {
    const [knotProgresses, setKnotProgresses] = useState({
        x: 10,
        y: map_range(190, 0, 216, 0, 100)
    })
    const [knotTwoProgress, setKnotTwoProgress] = useState({
        x: map_range(190, 0, 216, 0, 100),
        y: 10
    })
    const [gradientAngle, setGradeintAngle] = useState(0)
    const [lineCoordinates, setLineCoordinates] = useState({})
    const [gradientArea, setGradientArea] = useState(null)

    const gradientBoxRef = useRef(null)

    useEffect(()=> {

        const newX = knotTwoProgress.x - knotProgresses.x
        const newY =  knotTwoProgress.y - knotProgresses.y
        const newLength = Math.sqrt(Math.pow(newX, 2) + Math.pow(newY, 2))

        console.log(newX / newLength, newY / newLength, Math.asin(newX / newLength))
        setGradeintAngle(Math.atan(Math.abs((knotProgresses.y - knotTwoProgress.y) / (knotTwoProgress.x - knotProgresses.x))) * (180 / Math.PI))
        setLineCoordinates({
            startX: map_range(knotProgresses.x, 0, 100, 0, 216),
            startY: map_range(knotProgresses.y, 0, 100, 0, 216),
            endX: map_range(knotTwoProgress.x, 0, 100, 0, 216),
            endY: map_range(knotTwoProgress.y, 0, 100, 0, 216)
        })

    }, [knotTwoProgress, knotProgresses])

    useEffect(() => {
        if(gradientBoxRef !== null) {
            const boundingBox = gradientBoxRef.current.getBoundingClientRect()
            setGradientArea(boundingBox)
        }
    }, [gradientBoxRef])

    function handlePositionChange(e, setState, state) {
        

        if(gradientArea !== null) {
            const knotPosX = e.x - gradientArea.x
            const knotPosY = e.y - gradientArea.y
           console.log(knotPosY)

            if(knotPosX > 0 && knotPosY > 0){
                if(knotPosX < 216 || knotPosY < 216){
                    if(knotPosX < 216 && knotPosY > 216) {
                        setState({...state, x: map_range(knotPosX, 0, 216, 0, 100)})
                    }else if(knotPosX > 216 && knotPosY < 216){
                        setState({...state, y: map_range(knotPosY, 0, 216, 0, 100)})
                    }else{
                        setState({x: map_range(knotPosX, 0, 216, 0, 100), y: map_range(knotPosY, 0, 216, 0, 100)})
                    }
                }else{
                    setState({y: 100, x: 100})
                }
            }else{
                if(knotPosX < 0 && knotPosY > 0){
                    setState({...state, x: 0})

                }else if(knotPosX > 0 && knotPosY < 0){
                    setState({...state, y: 0})
                }else{
                    setState({x: 0, y: 0})
                }
               
            }

           
        }
        
    }

    function  updatePosition(e) {
        console.log(e.x)

        switch (e.target.id) {
            case "knot1": 
                handlePositionChange(e, setKnotProgresses, knotProgresses)
            break;
            case "knot2":
                handlePositionChange(e, setKnotTwoProgress, knotTwoProgress)
            break;
        }
       
        
    }

    return(
       <div className="w-full h-full" ref={gradientBoxRef} style={{
           background: `linear-gradient(${gradientAngle}deg, rgba(0,0,0,1) ${knotProgresses.x}%, rgba(255,255,255,1) ${knotTwoProgress.x}%)`
       }}>
            <Draggable
            defaultPosition={{x: 10, y: 190}}
            onDrag={updatePosition}
        
        bounds="parent"
        >
            <div id="knot1" className="w-3 h-3  rounded-xl  bg-primary"></div>
          

        </Draggable>
        <Draggable
         onDrag={updatePosition}

         defaultPosition={{x: 190, y: 10}}
        bounds="parent"
        >
            
            <div id="knot2" className="w-3 h-3 rounded-xl bg-primary"></div>
          

        </Draggable>
       </div>
    )
    
}

export default GradientView